/*--------------BASE --------*/
/* } */ 


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}
body,
button,
input,
textarea{
  font-family: "Poppins", sans-serif;
  font-size: 0.938rem;
}

body {
  background-color : rgb(52, 21, 81);
  color: gray ;
}

/* color: var(--text-color) */
/* h1,h2,h3 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
} */

/* font-weight: var(--font-semi-bold) */

 ul {
  list-style: none;
 }
 a {
  text-decoration: none;
 }
 button {
  cursor: pointer;
  border: none;
  outline: none;
 }

 img {
  max-width: 100%;
  height: auto;
 }

/*--------------reuseable css class ----------*/

.section {
  padding: 6rem 0 2rem;
}
.section__title {
  font-size: 2.25rem;
  color: gray;
}
/* color: var(--title-color) */

.section__subtitle {
  display: block;
  font-size: small;
  margin-bottom: 4rem;
};
/* font-size: var(--small-font-size) */

.section__title,
.section__subtitle{
  text-align: center;
}

/*--------------------LAYOUT-----------------*/
.container {
  max-width: 968px;
  margin-left: auto;
  margin-right: auto;
};
.grid {
  display: grid;
  gap: 1.5rem;
}


/*----------------BUTTONS-----------------*/
 .button {
  display:  inline-block;
  background-color:  gray;
  color : white;
  padding: 1.25rem 2rem;
  border-radius: 1rem;
  font-weight: medium;
 }

 /* background-color:  var(--title-color) */

 /* color : var(--container-color) */

 /* font-weight: var(--font-medium) */
 .button:hover {
  background-color: black;
 }
 /* background-color: var(--title-color-dark) */
 .botton__icon {
  margin-left: 5px;
 }

 /* margin-left: var(--mb-0-5) */
 .button--flex {
  display: inline-flex;
  align-items: center;
 }
 .header {
  border: 1px solid red;
   width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: white
}

.nav {
  height : 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
}
.nav_logo,
.nav_toggle {
    color : gray;       
    font-weight: medium;
} 
.nav__list {
  display: flex;
  column-gap: 2rem;
} 

.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: small;
  color: grey;
  font-weight: medium;
  transition: .3s;

} 
.nav__icon,
.nav__close,
.nav__toggle {
    display: none;
}

/*Active link*/
.active-link,
.nav__link :hover {
    color:  red;
}; 


/*----------------BREAKPOINTS-----------*/

@media all and (min-width : 770px) and (max-width : 992px){
  .container {
   margin-left:70px;
   margin-right: 70px;
  }

}
@media all and (max-width : 768px){
  .header {
    top : initial;
    bottom: 0;
  }
  .container {
    margin-left:20px;
    margin-right: 50px;
   }
   .nav{
    height: 60px;
   }
   .nav__menu{
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color:  white;
    padding: 1rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: .3s;
   } 

   .show-menu{
    bottom: 0;
 }

.nav__list{
display: grid;
grid-template-columns: repeat(3,1fr);
gap : 2rem;
}
.nav__icon{
font-size: 1.2rem;
}
}